.enemy {
   width: 340px;
   &__name {
      text-align: center;
      height: 22px;
   }
   &__health-wrapper {
      display: flex;
      align-items: center;
   }
   &__health {
      display: inline-block;
      flex: 1 1 auto;
      height: 7px;
      border: 1px solid grey;
      border-radius: 3px;
      background-color: rgb(4, 100, 16);
      margin: 0 5px 0 0;
   }
   &__health-level {
      font-size: 15px;
   }
   &__img {
      width: 150px;
      height: 220px;
      margin: 0 auto;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
}
